* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.pc-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
  background: #151515;
  font-family: "Poppins", sans-serif;
}

.card {
  position: relative;
  width: 250px;
  height: 250px;

  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;

  transition-delay: 0.5s;
}
.card:hover {
  width: 480px;
  transition-delay: 0.5s;
}

.card .circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card .circle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background: #191919;
  border: 4px solid #f40103;
  box-shadow: #990000 0px 0px 40px 20px, #000000 0px 0px 50px 30px;
  transition: 0.5s, background 0.5s;
  transition-delay: 0.75s, 1s;
}

.card:hover .circle::before {
  transition-delay: 0.5s;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-color: #f40103;
}

.card .circle .logo {
  position: relative;
  width: 200px;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.card:hover .circle .logo {
  transform: scale(0);
  transition-delay: 0s;
}

.card .product_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0) rotate(315deg);
  height: 200px;
  transition: 0.5s ease-in-out;
}

.card:hover .product_img {
  transition-delay: 0.75s;
  top: 25%;
  left: 72%;
  height: 350px;
  transform: translate(-50%, -50%) scale(1) rotate(15deg);
}

.card .content {
  position: absolute;
  width: 50%;
  left: 20%;
  padding: 20px 20px 20px 40px;
  opacity: 0;
  transition: 0.5s;
  visibility: hidden;
}

.card:hover .content {
  transition-delay: 0.75s;
  opacity: 1;
  visibility: visible;
  left: 20px;
}

.card .content h2 {
  color: #fff;
  text-transform: uppercase;
  font-size: 1.8em;
  line-height: 1em;
  margin-bottom: 10px;
  position: relative;
}

.card .content p {
  color: white;
  font-size: 0.8em;
}

.card .content a {
  position: relative;
  color: #111;
  background: #fff;
  padding: 5px 10px;
  border-radius: 10px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  margin-top: 10px;
}
